@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@500&display=swap');

* {
  margin: 0;
  padding: 0;
  border: none;
  font-family: 'Poppins', sans-serif;
  text-decoration: none;
  list-style-type: none;
  color: currentColor;
}

.main {
  padding-top: 5rem;
  z-index: -1;
  display: flex;
  flex-direction: column;
  align-items: center;
  color: #333;
}

.main h1 {
  margin-bottom: 1rem;
}

.main h1 {
  align-self: flex-start;
  margin-left: 1.5rem;
}

.main h1 p {
  color: #1139b0;
  font-size: 1rem;
}

.tabelaMundial-titulos th,
.tabelaBrasil-titulos th {
  padding: 0.5rem 0;
  text-align: start;
}

.tabelaMundial h3,
.tabelaBrasil h3 {
  background-color: #FFD9E9;
  padding: 10px;
  text-align: center;
}

.containerSelect {
  margin: 1rem 0;
  display: flex;
  color: #1139b0;
  font-weight: 600;
  justify-content: space-evenly;
}

.tabelaMundial-infos,
.tabelaBrasil-infos {
  width: 90%;
  margin: 5px auto;
}

.scroll {
  overflow-y: scroll;
  height: 50rem;

}

.tabelaMundial-infos thead,
.tabelaBrasil-infos thead {
  border: 2px solid #FFD9E9;
}
thead hr {
  border: 0.03rem solid #FF75AF;
  width: 274%;
}

.tabelaMundial-paises,
.tabelaBrasil-estados {
  height: 3rem;
}

tbody hr {
  border: 0.03rem solid #FFD9E9;
  width: 274%;
}

.paises {
  text-align: start;
}

@media only screen and (min-width:768px) {
  .main {
    width: 100%;
  }
  thead hr,
  tbody hr {
    width: 80%;
    position: relative;
  }
  .containerSelect {
    justify-content: start;
    margin-left: 4rem;
  }
  .containerSelect select {
    border: 1px solid #e7ecfd;
    outline-color: #1139b0;
  }
  .select-state {
    margin-left: 1rem;
  }
  .tabelaBrasil,
  .tabelaMundial {
    width: 90%;
  }
}